'use client'

import { useState } from 'react'
import Header from '@/components/header'
import Footer from '@/components/footer'
import LFSRCard from '@/components/lfsr-card'
import FSMCard from '@/components/fsm-card'

interface LFSRState {
  outputs: number[]
  states: number[][]
  practical_period: number
  theoretical_period: number
  n_bits: number
}

export default function Home() {
  const [r1, setR1] = useState<LFSRState | null>(null)
  const [r2, setR2] = useState<LFSRState | null>(null)
  const [r3, setR3] = useState<LFSRState | null>(null)

  return (
    <main className="min-h-screen flex flex-col bg-gradient-to-b from-slate-950 via-purple-950/40 to-slate-950">
      <Header />
      
      <div className="flex-1 max-w-6xl mx-auto w-full px-4 py-8 space-y-8">
        <div className="grid grid-cols-1 lg:grid-cols-3 gap-6">
          <LFSRCard
            title="R1 (Controller)"
            labels={['S2', 'S1', 'S0']}
            defaultState={[0, 0, 1]}
            defaultTaps={[0, 2]}
            onGenerate={setR1}
          />
          <LFSRCard
            title="R2"
            labels={['S3', 'S2', 'S1', 'S0']}
            defaultState={[1, 0, 1, 1]}
            defaultTaps={[0, 1]}
            onGenerate={setR2}
          />
          <LFSRCard
            title="R3"
            labels={['S4', 'S3', 'S2', 'S1', 'S0']}
            defaultState={[0, 1, 0, 0, 1]}
            defaultTaps={[0, 1, 2, 4]}
            onGenerate={setR3}
          />
        </div>

        {r1 && r2 && r3 && (
          <FSMCard r1={r1} r2={r2} r3={r3} />
        )}
      </div>

      <Footer />
    </main>
  )
}
